.error{
  // background: url(/../assets/img/bg-error2.jpeg) no-repeat;
  background: #E0E0E0;
    background-position: center top;
    background-size: cover;
    height: 100vh;
    // width: 100rem;
  // max-height: 100vh;
  position: relative;
  z-index: 2;
&__inner{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

&__items{
  padding: 3rem 4rem;
  max-width: 50rem;
  background: $color2;
  // border-radius: 1rem;
  // opacity: .8;
  border-radius: 50px;
background: #e0e0e0;
box-shadow:  20px 20px 60px #bebebe,
             -20px -20px 60px #ffffff;
  
}
&__text{
  color: #8f3228;
  opacity: .6;
  font-size: 2.4rem;
  margin-bottom: 3rem;
}
&__link{
  // background: #ed7163;
  background: #E0E0E0;
  padding: 1rem 2rem;
  border-radius: 1rem;
  opacity: 1;
  color: #8f3228;
  box-shadow: 0 .7rem 1rem #585858;

transition: all .3s;
  &:hover{
    background: #ed7163;
  color: $color3;

    // box-shadow: .3rem .3rem .5rem #8f3228;
    box-shadow: 0 .2rem 1rem #585858;

  }
  &:active{
    
    box-shadow: inset .3rem .3rem 1rem #8f3228;
  }

}
}