@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
body {
  background: #fff;
  font-family: "Roboto", sans-serif;
  font-size: 1.8rem;
  line-height: 2.2rem;
  font-weight: 400;
  color: #000;
}

.page {
  position: relative;
}

.up {
  position: fixed;
  bottom: 1rem;
  right: 4rem;
  max-width: 2.5rem;
  z-index: 1;
}
.up__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  opacity: 0.4;
  transition: all 0.3s;
}
.up__link:hover {
  opacity: 1;
}
.up__link:hover .up__link-text {
  visibility: visible;
}
.up__link-text {
  visibility: hidden;
  font-size: 1.4rem;
  color: #ED7163;
}

.wrapper {
  background: #F6F6F6;
}

.inner {
  padding: 12rem 0;
}

a {
  color: #828282;
}

.container {
  max-width: 94rem;
  padding: 0 1rem;
  margin: 0 auto;
  text-align: center;
}

.btn {
  display: inline-block;
  background: #828282;
  line-height: 4rem;
  color: #F6F6F6;
  text-align: center;
  transition: color 0.3s;
}
.btn:hover {
  color: #000;
}

.title {
  font-size: 3.4rem;
  line-height: 4.2rem;
  font-weight: 500;
  color: #000;
  line-height: 0.2rem;
  display: inline-block;
  padding: 2rem 2rem 0 2rem;
  background-color: #ea5241;
}

.header__inner {
  padding: 3rem 0 4rem;
  border-bottom: 1px solid #828282;
}

.menu__btn {
  display: none;
}
.menu__list {
  display: flex;
  justify-content: space-between;
}
.menu__list-link {
  transition: color 0.3s;
}
.menu__list-link:hover {
  color: #000;
}

.hero__inner {
  padding: 4rem 0 10rem;
}
.hero__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
}
.hero__top a {
  color: #828282;
  transition: color 0.3s;
}
.hero__top a:hover {
  color: #000;
}
.hero__title {
  max-width: 10rem;
  font-size: 4rem;
  line-height: 5.4rem;
  font-weight: 900;
  color: #000;
}
.hero__text {
  max-width: 18.5rem;
}
.hero__lang {
  vertical-align: middle;
  transform: rotate(-90deg) translate(50%, -100%);
  transform-origin: right top;
}
.hero__photo {
  width: 100%;
  min-height: 39rem;
  background: url(/../assets/img/photo.jpg) no-repeat;
  background-position: center center;
  background-size: cover;
}

.about__title {
  margin-bottom: 2.5rem;
}
.about__items {
  margin: 0 auto;
  max-width: 60rem;
}
.about__items p {
  margin-bottom: 2rem;
}
.about__items a {
  transition: color 0.3s;
}
.about__items a:hover {
  color: #000;
}

.skills__items {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-auto-flow: row;
  gap: 2rem;
}
.skills__title {
  margin-bottom: 7rem;
}
.skills__item {
  max-width: 17rem;
  display: flex;
  align-items: center;
  justify-content: baseline;
  flex-direction: column;
  margin-bottom: 2rem;
}
.skills__item--img {
  max-width: 8rem;
  max-height: 8rem;
  margin-bottom: 2.5rem;
}
.skills__item--title {
  font-size: 1.6rem;
  line-height: 1.6rem;
  font-weight: 400;
  color: #828282;
}
.skills__item--text {
  max-width: 100%;
  word-wrap: break-word;
}

.portfolio__title {
  margin-bottom: 4.7rem;
}
.portfolio__item {
  margin-bottom: 10rem;
  box-shadow: 0 0.3rem 1rem rgba(192, 192, 192, 0.1);
}
.portfolio__item:last-child {
  margin-bottom: 0;
}
.portfolio__link {
  display: inline-block;
  margin-bottom: 2.5rem;
  max-width: 100%;
  max-height: 52rem;
}
.portfolio__text--link {
  display: block;
  margin-bottom: 0.5rem;
  text-decoration: underline;
  transition: color 0.3s;
}
.portfolio__text--link:hover {
  color: #000;
}
.portfolio__description {
  padding-bottom: 1rem;
  font-size: 1.5rem;
}
.portfolio__description a {
  transition: color 0.3s;
}
.portfolio__description a:hover {
  color: #000;
}

.certificates__title {
  margin-bottom: 7rem;
}
.certificates__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}
.certificates .certificates__link {
  box-shadow: 0 0.3rem 1rem rgba(192, 192, 192, 0.2);
}

.footer__title {
  margin-bottom: 3rem;
}
.footer__text {
  margin-bottom: 3rem;
}
.footer__mail {
  display: inline-block;
  font-size: 1.8rem;
  line-height: 2rem;
  font-weight: 700;
  color: #fff;
  background: #000;
  padding: 1.2rem 3rem;
  margin-bottom: 9rem;
  border-radius: 3rem;
  transition: background 0.3s;
}
.footer__mail:hover {
  background: #ea5241;
  color: #000;
}
.footer__bottom {
  color: #828282;
}

.social {
  display: flex;
  justify-content: center;
  margin-bottom: 5rem;
}
.social__link {
  display: block;
  width: 7rem;
  transition: transform 0.3s;
}
.social__link:hover {
  transform: scale(1.2);
}
.social__link + .social__link {
  margin-left: 6rem;
}

.error {
  background: #E0E0E0;
  background-position: center top;
  background-size: cover;
  height: 100vh;
  position: relative;
  z-index: 2;
}
.error__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.error__items {
  padding: 3rem 4rem;
  max-width: 50rem;
  background: #828282;
  border-radius: 50px;
  background: #e0e0e0;
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
}
.error__text {
  color: #8f3228;
  opacity: 0.6;
  font-size: 2.4rem;
  margin-bottom: 3rem;
}
.error__link {
  background: #E0E0E0;
  padding: 1rem 2rem;
  border-radius: 1rem;
  opacity: 1;
  color: #8f3228;
  box-shadow: 0 0.7rem 1rem #585858;
  transition: all 0.3s;
}
.error__link:hover {
  background: #ed7163;
  color: #F6F6F6;
  box-shadow: 0 0.2rem 1rem #585858;
}
.error__link:active {
  box-shadow: inset 0.3rem 0.3rem 1rem #8f3228;
}